import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

function Navbar() {

  const homeUrl = "/";
  const blogUrl = "/blog";
  const episodesUrl = "/episodes";
  const campaignNotesUrl = "/campaign-notes";
  const theWorldUrl = `${campaignNotesUrl}/the-world`;
  const theRecapUrl = `${campaignNotesUrl}/the-recap`;
  const meetTheCrewUrl = "/meet-the-crew";

  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark sticky-top">
      <div className="container-fluid">
        <Link className="navbar-brand" to={homeUrl}>Dice Droppers</Link>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <Link className="nav-link active" aria-current="page" to={homeUrl}>Home</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to={blogUrl}>Blog</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to={episodesUrl}>Episodes</Link>
            </li>
            <li className="nav-item dropdown">
              <Link className="nav-link dropdown-toggle" to={campaignNotesUrl} id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                Campaign Notes
              </Link>
              <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                <li><Link className="dropdown-item bg-dark text-light border-0" to={theWorldUrl}>The World</Link></li>
                <li><Link className="dropdown-item" to={`${theWorldUrl}/the-continent`}>The Continent</Link></li>
                <li><Link className="dropdown-item" to={`${theWorldUrl}/the-factions`}>The Factions</Link></li>
                <li><Link className="dropdown-item" to={`${theWorldUrl}/the-baddies`}>The Baddies</Link></li>
                <li><Link className="dropdown-item bg-dark text-light border-0" to={theRecapUrl}>The Recap</Link></li>
                <li><Link className="dropdown-item" to={`${theRecapUrl}/chapter-one`}>Chapter One</Link></li>
                <li><Link className="dropdown-item" to={`${theRecapUrl}/chapter-two`}>Chapter Two</Link></li>
                <li><Link className="dropdown-item" to={`${theRecapUrl}/chapter-three`}>Chapter Three</Link></li>
              </ul>
            </li>
            <li className="nav-item dropdown">
              <Link className="nav-link dropdown-toggle" to={meetTheCrewUrl} id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                Meet The Crew
              </Link>
              <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                <li><span className="dropdown-item bg-dark text-light border-0">The Dungeon Master</span></li>
                <li><Link className="dropdown-item" to={`${meetTheCrewUrl}/lawson`}>Lawson</Link></li>
                <li><span className="dropdown-item bg-dark text-light border-0">The Cast</span></li>
                <li><Link className="dropdown-item" to={`${meetTheCrewUrl}/chris`}>Chris</Link></li>
                <li><Link className="dropdown-item" to={`${meetTheCrewUrl}/fabbs`}>Fabbs</Link></li>
                <li><Link className="dropdown-item" to={`${meetTheCrewUrl}/hailey`}>Hailey</Link></li>
                <li><Link className="dropdown-item" to={`${meetTheCrewUrl}/luke`}>Luke</Link></li>
                <li><Link className="dropdown-item" to={`${meetTheCrewUrl}/oscar`}>Oscar</Link></li>
              </ul>
            </li>
          </ul>
          <form className="d-flex">
            <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
            <button className="btn btn-outline-light" type="submit">Search</button>
          </form>
        </div>
      </div>
    </nav>
  );
}

export default Navbar;